.workExperience {
    padding: 20px;
  }
  
  .workExperience h2 {
    font-size: 24px;
  }
  
  .workCard {
    border: 1px solid #ddd;
    padding: 15px;
    margin: 10px 0;
    border-radius: 5px;
  }
  
  .addButton {
    background-color: #6200ea;
    color: white;
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
  }
  
  .modal {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    background-color: white;
    padding: 20px;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .modal input,
  .modal textarea {
    display: block;
    margin: 10px 0;
    padding: 10px;
    width: 100%;
    border: 1px solid #ddd;
    border-radius: 5px;
  }
  
  .saveButton,
  .cancelButton {
    padding: 10px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px;
  }
  
  .saveButton {
    background-color: #6200ea;
    color: white;
  }
  
  .cancelButton {
    background-color: #ccc;
  }
  